<template>
  <el-tabs
    :value="routerPath"
    type="card"
    @tab-remove="removeTab"
    stretch
    @tab-click="tabClick"
  >
    <el-tab-pane
      v-for="(item,index) in editableTabs"
      :key="item.name"
      :label="item.title"
      :name="item.name"
      :closable="index === 0 ? false : true"
    >
    </el-tab-pane>
  </el-tabs>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'TabsView',
  data () {
    return {

    }
  },
  computed: {
    ...mapState('tabsView', ['routerPath', 'editableTabs'])
  },
  methods: {
    removeTab (targetName) {
      // console.log(targetName)
      this.$store.dispatch('tabsView/removeTab', targetName)
    },
    tabClick (tab) {
      // console.log(tab.index)
      const path = this.editableTabs[tab.index].name
      this.$store.commit('tabsView/setRouterPath', path)
    }
  }
}
</script>

