import Vue from 'vue'
import Vuex from 'vuex'
import tabsView from './modules/tabsView'
import createVuexPersisted from 'vuex-persistedstate'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
  },
  getters: {
  },
  mutations: {
  },
  actions: {
  },
  modules: {
    tabsView
  },
  plugins: [
    createVuexPersisted({
      reducer (val) {
        return {
          tabsView: {
            routerPath: val.tabsView.routerPath,
            editableTabs: val.tabsView.editableTabs
          }
        }
      }
    })
  ]
})
