import router from '@/router'
export default {
  namespaced: true,
  state: {
    routerPath: '/dashboard',
    editableTabs: [{
      title: '工作台',
      name: '/dashboard'
    }]
  },
  mutations: {
    addTabs (state, payload) {
      state.editableTabs.push(payload)
    },
    removeTabs (state, index) {
      state.editableTabs.splice(index, 1)
    },
    setRouterPath (state, path) {
      state.routerPath = path
    }
  },
  actions: {
    addTab (context, path) {
      const tab = router.options.routes[1].children.find(item => item.path === path).meta
      if (context.state.editableTabs.every(item => item.name !== path)) {
        context.commit('addTabs', {
          title: tab.title,
          name: path
        })
      }
      context.commit('setRouterPath', path)
    },
    removeTab (context, targetName) {
      const index = context.state.editableTabs.findIndex(item => item.name === targetName)
      const tabs = context.state.editableTabs
      let activeName = context.state.routerPath
      // 如果要删除的刚好是被选中的 才需要确定下一个选中的值 否则就会直接删除掉
      if (activeName === targetName) {
        tabs.forEach((tab, index) => {
          if (tab.name === targetName) {
            const nextTab = tabs[index + 1] || tabs[index - 1]
            if (nextTab) {
              activeName = nextTab.name
            }
          }
        })
      }
      context.commit('removeTabs', index)
      context.commit('setRouterPath', activeName)
    }
  }
}
